import userInput from "./userInput";
import constants from "./constants.toml";

const {
  group_types,
  level_intervals,
  scores_by_row,
  new_level_after,
  group_templates,
} = constants;

const getDimensions = (groupType) =>
  group_templates[groupType].reduce(
    (acc, [x, y]) => [Math.max(acc[0], x + 1), Math.max(acc[1], y + 1)],
    [0, 0]
  );

const getRandomGroup = () =>
  group_types[Math.floor(Math.random() * group_types.length)];

let uniqueId = 0;

const engine = (el, blockSize, columns, rows, onUpdate) => {
  let groups = [];

  let cancelDrag = () => {};

  let currentLevel = 0;
  let linesCleared = 0;
  let score = 0;

  let interval = level_intervals[0];

  const canvas = new Array(columns)
    .fill(null)
    .map(() => new Array(rows).fill(null));

  const hasCollision = ({ group, x, y }) =>
    group.some(
      ([_x, _y]) =>
        x + _x > columns ||
        x + _x < 0 ||
        (y + _y >= 0 && canvas[x + _x]?.[y + _y] !== null)
    );

  const update = (cured) => onUpdate(canvas, groups, score, currentLevel + 1);

  const clearCompletedRows = () => {
    const prevLinesCleared = linesCleared;
    for (let y = 0; y < rows; y++) {
      if (canvas.every((column) => column[y] !== null)) {
        canvas.forEach((column) => column.splice(y, 1));
        canvas.forEach((column) => column.unshift(null));
        linesCleared++;
      }
    }
    if (linesCleared > prevLinesCleared) {
      score += scores_by_row[linesCleared - prevLinesCleared - 1];
      console.log(linesCleared);
      if (linesCleared % new_level_after === 0) {
        currentLevel++;
        clearInterval(gameInterval);
        interval = level_intervals[currentLevel];
        gameInterval = createInterval();
      }
    }
  };

  const cureCanvas = () => {
    if (!groups.length) return;
    const currentGroup = groups[groups.length - 1];
    currentGroup.group.forEach(([dx, dy]) => {
      canvas[currentGroup.x + dx][currentGroup.y + dy] = currentGroup.groupType;
    });
    clearCompletedRows();
  };

  const addGroup = () => {
    cancelDrag();
    const groupType = getRandomGroup();
    const [width, height] = getDimensions(groupType);
    const group = group_templates[groupType];
    const entryPoint = Math.floor(columns / 2 - width / 2);
    const newGroup = {
      id: uniqueId++,
      groupType,
      group,
      rotation: 0,
      x: entryPoint,
      y: -height,
    };
    const nextGroup = {
      ...newGroup,
      y: newGroup.y + 1,
    };
    if (hasCollision(newGroup) || hasCollision(nextGroup)) {
      clearInterval(gameInterval);
      onUpdate(false);
      return;
    }
    cureCanvas();

    groups.push(newGroup);
    setTimeout(() => {
      if (groups.length > 1) {
        groups.shift();
        update(true);
      }
    }, 100);
    update(true);
  };

  const moveDown = () => {
    if (!groups.length) return;
    const currentGroup = groups[groups.length - 1];
    const newGroup = {
      ...currentGroup,
      y: currentGroup.y + 1,
    };
    if (hasCollision(newGroup)) {
      addGroup();
      return;
    }
    groups[groups.length - 1] = newGroup;
    update();
  };

  const moveRight = () => {
    if (!groups.length) return;
    const currentGroup = groups[groups.length - 1];
    const newGroup = {
      ...currentGroup,
      x: currentGroup.x + 1,
    };
    if (hasCollision(newGroup)) {
      return;
    }
    groups[groups.length - 1] = newGroup;
    update();
  };

  const moveLeft = () => {
    if (!groups.length) return;
    const currentGroup = groups[groups.length - 1];
    const newGroup = {
      ...currentGroup,
      x: currentGroup.x - 1,
    };
    if (hasCollision(newGroup)) {
      return;
    }
    groups[groups.length - 1] = newGroup;
    update();
  };

  const dropRight = () => {
    if (!groups.length) return;
    const currentGroup = groups[groups.length - 1];
    let newGroup = {
      ...currentGroup,
      x: currentGroup.x + 1,
    };
    while (!hasCollision(newGroup)) {
      groups[groups.length - 1] = newGroup;
      newGroup = {
        ...newGroup,
        x: newGroup.x + 1,
      };
    }
    update();
  };

  const dropLeft = () => {
    if (!groups.length) return;
    const currentGroup = groups[groups.length - 1];
    let newGroup = {
      ...currentGroup,
      x: currentGroup.x - 1,
    };
    while (!hasCollision(newGroup)) {
      groups[groups.length - 1] = newGroup;
      newGroup = {
        ...newGroup,
        x: newGroup.x - 1,
      };
    }
    update();
  };

  const dropDown = () => {
    if (!groups.length) return;
    const currentGroup = groups[groups.length - 1];
    let newGroup = {
      ...currentGroup,
      y: currentGroup.y + 1,
    };
    while (!hasCollision(newGroup)) {
      groups[groups.length - 1] = newGroup;
      newGroup = {
        ...newGroup,
        y: newGroup.y + 1,
      };
    }
    update();

    addGroup();
  };

  function rotate() {
    if (!groups.length) return;
    const currentGroup = groups[groups.length - 1];
    // Find center point of the piece
    const sumX = currentGroup.group.reduce((sum, [x]) => sum + x, 0);
    const sumY = currentGroup.group.reduce((sum, [_, y]) => sum + y, 0);
    const centerX = Math.round(sumX / currentGroup.group.length);
    const centerY = Math.round(sumY / currentGroup.group.length);

    const newGroup = {
      ...currentGroup,
      rotation: currentGroup.rotation + 90,
      group: currentGroup.group.map(([x, y]) => {
        // Translate to origin, rotate, translate back
        const relativeX = x - centerX;
        const relativeY = y - centerY;
        return [centerX - relativeY, centerY + relativeX];
      }),
    };

    if (hasCollision(newGroup)) {
      return;
    }
    groups[groups.length - 1] = newGroup;
    update();
  }

  const createInterval = () => setInterval(moveDown, interval);

  let gameInterval = createInterval();

  addGroup();
  update();

  cancelDrag = userInput(el, blockSize, {
    moveLeft,
    moveRight,
    rotate,
    moveDown,
    dropDown,
    dropRight,
    dropLeft,
  });
};

export default engine;
