import { useState, useCallback } from "react";
import engine from "./engine";

const useEngine = (columns, rows, canvasRef, blockSize) => {
  const [gameProps, setGameProps] = useState(null);
  const onUpdate = useCallback((canvas, groups, score, currentLevel) => {
    if (!canvas) {
      alert("Game Over");
      setGameProps(null);
    }
    setGameProps({ canvas, groups, score, currentLevel });
  }, []);

  const start = useCallback(() => {
    engine(canvasRef.current, blockSize, columns, rows, onUpdate);
  }, [columns, rows, onUpdate, blockSize]);

  return { ...gameProps, start };
};

export default useEngine;
