import React from "react";

import * as styles from "./styles.module.css";
import constants from "./constants.toml";
const { colours } = constants;

const CurrentGroup = ({ blockSize, groups }) =>
  groups.map(({ group, x, y, groupType, id }) => (
    <div
      key={id}
      className={styles.group}
      style={{
        position: "absolute",
        left: 0,
        top: 0,
        transform: `translate3d(${x * blockSize}px, ${y * blockSize}px, 0)`,
      }}
    >
      {group.map(([_x, _y]) => (
        <div
          style={{
            position: "absolute",
            left: `${_x * blockSize}px`,
            top: `${_y * blockSize}px`,
            width: blockSize,
            height: blockSize,
            backgroundColor: colours[groupType],
          }}
        />
      ))}
    </div>
  ));

export default CurrentGroup;
