import React, { useRef, useState, useEffect, forwardRef, useCallback } from "react";

import * as styles from "./styles.module.css";
import constants from "./constants.toml";
const { colours } = constants;

var elem = document.documentElement;

function openFullscreen() {
  if (elem.requestFullscreen) {
    elem.requestFullscreen();
  } else if (elem.webkitRequestFullscreen) { /* Safari */
    elem.webkitRequestFullscreen();
  } else if (elem.msRequestFullscreen) { /* IE11 */
    elem.msRequestFullscreen();
  }
}

const Canvas = forwardRef(
  ({ children, canvas, start, rows, blockSize }, canvasRef) => {
    const startGame = useCallback(() => {
      openFullscreen();
      start();
    }, [start]);
    return (
      <div
        ref={canvasRef}
        className={styles.canvas}
        style={{ "--blockSize": blockSize + "px", height: blockSize * rows }}
      >
        {!canvas && <button className={styles.newGame} onClick={startGame}>New Game</button>}

        {canvas &&
          canvas.map((row, x) =>
            row.map((cell, y) => (
              <div
                key={`canvas${x}${y}`}
                className={styles.block}
                style={{
                  position: "absolute",
                  transform: `translate3d(${x * blockSize}px, ${
                    y * blockSize
                  }px, 0)`,
                  backgroundColor: cell ? colours[cell] : "#555",
                }}
              />
            ))
          )}
        {children}
      </div>
    );
  }
);

export default Canvas;
