import React, { useRef, useState, useEffect } from "react";
import * as styles from "./styles.module.css";

import Canvas from "./Canvas";
import CurrentGroup from "./CurrentGroup";
import useEngine from "./useEngine";
import useBlockSize from "./useBlockSize";

const columns = 11;
const rows = 20;

const App = () => {
  const canvasRef = useRef(null);

  const blockSize = useBlockSize(canvasRef, columns, rows);

  const { canvas, groups, score, start, currentLevel } = useEngine(
    columns,
    rows,
    canvasRef,
    blockSize
  );

  return (
    <div className={styles.container}>
      <div className={styles.meta}>
        <span>{currentLevel}</span>
        <span>{score}</span>
      </div>
      <Canvas
        ref={canvasRef}
        canvas={canvas}
        columns={columns}
        rows={rows}
        blockSize={blockSize}
        start={start}
      >
        {groups?.length && (
          <CurrentGroup blockSize={blockSize} groups={groups} />
        )}
      </Canvas>
    </div>
  );
};

export default App;
