function trackFingerMovement(element, distance, callback) {
  let startX = 0;
  let startY = 0;
  let lastTriggerX = 0;
  let lastTriggerY = 0;
  let isDragging = false;
  let isTap = true;
  let startNow = 0;

  element.addEventListener("touchstart", (e) => {
    console.log("start");
    isDragging = true;
    isTap = true;
    startX = e.touches[0].clientX;
    startY = e.touches[0].clientY;
    lastTriggerX = startX;
    lastTriggerY = startY;
    startNow = Date.now();
  });

  element.addEventListener(
    "touchmove",
    (e) => {
      e.preventDefault(); // Prevent scrolling
      e.stopPropagation();
      if (!isDragging) return;
      console.log("there", isDragging);

      const currentX = e.touches[0].clientX;
      const currentY = e.touches[0].clientY;

      // Check X axis movement
      const distanceX = currentX - lastTriggerX;
      if (Math.abs(distanceX) >= distance) {
        isTap = false;
        callback({
          axis: "x",
          direction: distanceX > 0 ? "right" : "left",
          distance: Math.abs(distanceX),
        });
        lastTriggerX = currentX;
      }

      // Check Y axis movement
      const distanceY = currentY - lastTriggerY;
      if (Math.abs(distanceY) >= distance) {
        isTap = false;
        callback({
          axis: "y",
          direction: distanceY > 0 ? "down" : "up",
          distance: Math.abs(distanceY),
        });
        lastTriggerY = currentY;
      }
    },
    { passive: false }
  );

  element.addEventListener("touchend", () => {
    if (!isDragging) return;
    if (isTap) {
      callback({ isTap });
    }

    const elapsed = Date.now() - startNow;
    const distanceX = lastTriggerX - startX;
    const distanceY = lastTriggerY - startY;
    const velocityX = distanceX / elapsed;
    const velocityY = distanceY / elapsed;
    // get bigger of the two:
    const axis = Math.abs(velocityX) > Math.abs(velocityY) ? "x" : "y";
    const velocity =
      Math.abs(velocityX) > Math.abs(velocityY) ? velocityX : velocityY;
    const direction =
      axis === "x"
        ? velocity > 0
          ? "right"
          : "left"
        : velocity > 0
        ? "down"
        : "up";

    if (elapsed < 140 && Math.abs(velocity) > 0.4) {
      callback({
        axis,
        direction,
        isFlick: true,
      });
    }

    isDragging = false;
  });

  return () => {
    isDragging = false;
  };
}

const userInput = (el, blockSize, gameControls) => {
  const {
    moveLeft,
    moveRight,
    rotate,
    moveDown,
    dropDown,
    dropRight,
    dropLeft,
  } = gameControls;
  const cancelDrag = trackFingerMovement(
    el,
    blockSize,
    ({ axis, isTap, isFlick, direction }) => {
      if (isFlick) {
        if (direction === "down") {
          dropDown();
        } else if (direction === "up") {
          rotate();
        } else if (direction === "left") {
          dropLeft();
        } else if (direction === "right") {
          dropRight();
        }
      } else if (axis === "x") {
        if (direction === "right") {
          moveRight();
        } else {
          moveLeft();
        }
      } else if (axis === "y") {
        if (direction === "down") {
          moveDown();
        }
      } else if (isTap) {
        rotate();
      }
    }
  );

  document.addEventListener("keydown", (event) => {
    if (event.key === "ArrowRight") {
      console.log("move right");
      moveRight();
    }
    if (event.key === "ArrowLeft") {
      moveLeft();
    }
    if (event.key === "ArrowUp") {
      rotate();
    }
    if (event.key === "ArrowDown") {
      dropDown();
    }
  });
  return cancelDrag;
};

export default userInput;
